
.wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
}

.accordion{
    width:50%;
}
.item{
    
    padding:10px 20px;
}

.title{
    display:flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
 
.content{
    color:gray;
    max-height: 0;
    overflow: hidden;
    transition:all 0.5s cubic-bezier(1,0,1,0);
    font-size: 14px;
}
.content.show{
height: auto;
max-height: 9999px;
transition: all 0.5s cubic-bezier(1,0,1,0);
}